.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    padding-right: 15px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
  }

  .quantity {
    display: flex;
    padding-left: 20px;

    .arrow {
      margin: 0 10px;
      cursor: pointer;
      user-select: none;
      transition: color 200ms ease-in-out;

      &:hover {
        color: #4285f4;
      }
    }
  }

  .remove-button {
    padding-left: 12px;
    cursor: pointer;
    user-select: none;
    transition: color 200ms ease-in-out;

    &:hover {
      color: #4285f4;
    }
  }
}
